@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative;
  background-repeat: repeat;
  background-size: cover; }
  .App .background {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    z-index: -50; }
  .App header {
    position: relative;
    height: 40vh; }
    .App header nav a .kangaroo {
      position: absolute;
      left: 15px;
      top: 10px;
      height: 60px; }
    .App header nav button .hamburger {
      position: fixed;
      right: 5vw;
      top: 3vh;
      height: 35px;
      z-index: 51; }
    .App header .main-logo {
      position: absolute;
      left: 50%;
      top: 60%;
      transform: translate(-50%, -50%);
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px; }
      .App header .main-logo h1 {
        font-size: 25px; }
      .App header .main-logo h2 {
        font-size: 46px; }
  .App main {
    padding-top: 18px;
    padding-bottom: 0;
    background-color: #000000; }
  .App section.about-me {
    position: relative;
    min-height: 100vh; }
    .App section.about-me .about-me-wrap {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 75vw;
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px; }
      .App section.about-me .about-me-wrap .top h2 {
        font-size: 23px; }
      .App section.about-me .about-me-wrap .top h1 {
        font-size: 49px; }
      .App section.about-me .about-me-wrap .body {
        font-family: 'Permanent Marker', cursive;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 3px; }
  .App footer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-color: #000000; }
    .App footer p {
      margin-top: 12px;
      line-height: 10px;
      color: #6969696e;
      font-size: 10px;
      letter-spacing: 3px;
      font-family: Arial, "Helvetica Neue", Helvetica, sans-serif; }

nav.menu-hamburger {
  position: fixed;
  left: 100%;
  top: 0;
  list-style: none;
  /* transition: 1s; */
}

li {
  list-style: none;
  margin-bottom: 13vh;
  text-transform: uppercase;
  letter-spacing: 4px;
  font-size: 1.7rem;
}

nav.active {
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 23vh;
  list-style: none;
  text-align: center;
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: #000000;
  z-index: 50;
  transition: .3s;
}
